import {
	CreateOrder
} from '../../common/api.js'
export default {
	data() {
		return {
			settArr: [],
			defaAdd: null,
			totalPri: 0,
			productData: [],
			pay_type: 0
		}
	},
	computed: {
		count() {
			// console.log(this.$store.state.settArr)
			return this.$store.state.settArr;
		}
	},
	onLoad(option) {
		this.pay_type = option.pay_type;
		this.pageInit();
	},
	methods: {
		pageInit() {
			let _this = this;
			this.settArr = this.$store.state.settArr;
			this.defaAdd = this.$store.state.defaAdd;
			this.totalPri = this.$store.state.totalPri;
			this.settArr.forEach((e) => {
				if (e.commodity) {
					_this.productData.push({
						id: e.commodity.id,
						number: e.number
					})
				} else {
					_this.productData.push({
						id: e.id,
						number: e.number
					})
				}

			})
		},
		chooseAdd() {
			uni.navigateTo({
				url: "../chooseAddresss/chooseAddresss?pay_type=" + this.pay_type
			})
		},
		pay() {
			if (this.defaAdd) {
				let d = {
					price: this.totalPri * 100,
					shipping_info_id: this.defaAdd.id,
					productData: this.productData,
					order_payment_type: this.pay_type
				}
				CreateOrder(d).then(res => {
					if (res.status) {
						let cog = res.config;
						wx.requestPayment({
							timeStamp: cog.timeStamp,
							nonceStr: cog.nonceStr,
							package: cog.package,
							signType: cog.signType,
							paySign: cog.paySign,
							success(res) {
								switch (res.code) {
									case '200':
										uni.showToast({
											icon: "success",
											title: '支付成功'
										});
										break;
									case '201':
										uni.showToast({
											icon: "none",
											title: '订单创建失败'
										});
										break;
									case '202':
										uni.showToast({
											icon: "none",
											title: '创建订单商品失败'
										});
										break;
									case '203':
										uni.showToast({
											icon: "none",
											title: '商户错误'
										});
										break;
									case '204':
										uni.showToast({
											icon: "none",
											title: '网络错误'
										});
										break;
										case '205':
											uni.showToast({
												icon: "none",
												title: '库存数量不足'
											});
											break;

								}

							},
							fail(res) {}
						})
					}
				})
			}
		}
	}
}
